<template>
  <div class="test">
    <input type="tel" v-model="phone"
           @keyup="filter($event)"
           placeholder="请输入手机号码"
           maxlength="13"/>
    <button @click="clearPhone">清空</button>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        phone: ''
      }
    },
    methods: {
      filter (e) {
        var target = e.target
        // this.e = target.selectionStart
        var selectionStart = target.selectionStart
        if (e.keyCode !== 8) {
          this.phone = this.phone.replace(/\D/g, '').replace(/(^...(?!$)|....(?!$)\B)/g, '$1 ')
          var length = this.phone.length
          // console.log('【phone】 ' + length + ' 【selectionStart】 ' + selectionStart)
          if (selectionStart === length || selectionStart === length - 1) return
          if (selectionStart === 4 || selectionStart === 9) {
            this.resetSelection(target, selectionStart + 1)
          } else {
            this.resetSelection(target, selectionStart)
          }
        } else {
          var str = this.phone.replace(/\D/g, '')
          if (selectionStart === 3 || selectionStart === 8) { // 需要删除一个空格加上前面的数字
            var arr = str.split('')
            // console.log(arr)
            if (selectionStart === 3) {
              arr.splice(target.selectionStart - 1, 1)
            } else {
              arr.splice(target.selectionStart - 2, 1)
            }
            // console.log(arr)
            this.phone = arr.join('').replace(/\D/g, '').replace(/(^...(?!$)|....(?!$)\B)/g, '$1 ')
            this.resetSelection(target, selectionStart - 1)
          } else {
            this.phone = str.replace(/\D/g, '').replace(/(^...(?!$)|....(?!$)\B)/g, '$1 ')
            this.resetSelection(target, selectionStart)
          }
        }
      },
      resetSelection (target, selectionStart) {
        setTimeout(function () {
          target.selectionStart = selectionStart
          target.selectionEnd = selectionStart
        }, 50)
      },
      clearPhone () {
        this.phone = ''
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" type="text/css">
  @import "../assets/css/base";
  .test {
    padding-top: 80px;
    input {
      margin: 15px auto;
      padding-left: 5%;
      display: block;
      width: 80%;
      height: 250px;
      font-size: 110px;
      border: 1px solid #000; /*no*/
      border-radius: 35px;
    }
    button {
      margin: 0 auto;
      display: block;
      width: 150px;
      height: 150px;
      font-size: 38px;
    }
  }
</style>



