import Vue from 'vue'
import Router from 'vue-router'
import UI from '../components/ui/z-ui.vue'
import Test from '../components/test.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'test',
      component: Test
    }, {
      path: '/ui',
      name: 'ui',
      component: UI
    }
  ]
})



